@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-weight: 600;
  transition: all 0.8s;
  -webkit-transition: all 0.8s;
  -moz-transition: all 0.8s;
  -ms-transition: all 0.8s;
  -o-transition: all 0.8s;
  font-family: "Poppins", sans-serif;
}
body {
  background-color: aliceblue;
}
:root {
  /* #ff0000 */
  --base-color: #ffba03;
}
.app {
  max-width: 500px;
  width: 100%;

  display: flex;
  flex-direction: column;

  gap: 10px;
}
.apptotal {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-top: 3rem;
  height: 100vh;
  flex-wrap: wrap;
}
.container {
  background-color: white;
  height: 450px;
  box-shadow: 1px -1px 5px rgb(135, 135, 135);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  padding: 20px 50px;
  position: relative;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-thumb {
  background-color: var(--base-color);
  border-radius: 6px;
}
.app {
  display: flex;
}
h1 {
  text-align: center;
  border-bottom: 2px solid var(--base-color);
  font-weight: 800;
}
.headingspan {
  color: var(--base-color);
}
.friendlist {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
}
.friendli {
  list-style: none;
  padding: 10px;
  cursor: pointer;
  display: grid;
  grid-template-rows: 1fr 1fr;
  place-items: center;
}
.friendli img {
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
.friendli:hover {
  background-color: var(--base-color);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.addfriendbtn,
.uicolorbtn {
  margin: 0 auto;
  width: 50%;
  background-color: var(--base-color);
  padding: 10px;
  border: none;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  cursor: pointer;
}
.uicolorbtn {
  width: 200px;
}

.addfriendui,
.changeuiinputotal {
  max-width: 400px;
  width: 100%;
  height: 170px;
  margin: 0 auto;
  background-color: var(--base-color);
  padding: 10px 30px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.changeuiinputotal {
  height: 100px;
}
.addfriendui label {
  width: 50%;
}
.addfriendui input,
.changeuiinputotal input {
  background-color: rgb(255, 255, 255);
  outline: none;
  border: 2px solid var(--base-color);
  width: 60%;
  height: 35.6px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  padding: 7px;
  text-align: center;
}
.flex {
  display: flex;
  gap: 10px;
  margin-top: 5px;
}
.friendname {
  text-align: center;
}
.Add {
  background-color: #61c674;
  border: none;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 30px;
  height: 35.6px;
  border-radius: 5px;
  padding: 5px 0;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
}

.changeui {
  margin-top: 10rem;
}
